/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect, useState } from "react";
import styled from "styled-components";
import { TfiPencilAlt } from "react-icons/tfi";
import { MdDeleteOutline } from "react-icons/md";
import { HiOutlineArrowsPointingOut } from "react-icons/hi2";
import { BsArrowDownLeftSquare } from "react-icons/bs";
import { CgCloseR } from "react-icons/cg";
import { useDispatch } from "react-redux";
import { setEmpData } from "../../feature/EmploySlice";

const EmployeeTable2 = ({ data }) => {
  const [month, setMonth] = useState(new Date(data.expire).getMonth());
  const [year, setYear] = useState(new Date(data.expire).getFullYear());
  const [visible, setVisible] = useState(0);
  const dispatch = useDispatch();

  useEffect(() => {
    const nyear = new Date();
    var nyr = nyear.getFullYear();
    var nmo = nyear.getMonth();

    if (month < nmo) {
      setYear(year - 1);
      setMonth(month + 12 - nmo);
    } else {
      setMonth(month - nmo);
    }
    setMonth((year - nyr) * 12 + month);
  }, [visible]);

  const sentData = (evt) => {
    //getEmployeeData(data);
    let z = "";
    const m = month;
    if (m > 3) {
      z = "Safe";
    } else if (m < 0) {
      z = "Reject";
    } else {
      z = "Danger";
    }

    dispatch(setEmpData({ data: data, month: m, evt: evt, zone: z }));
    sessionStorage.setItem("data", btoa(JSON.stringify(data)));

    setVisible(0);
  };

  return (
    <TRow>
      <TData>
        <Image src={data.image} alt="avatar" />
      </TData>
      <TData>{data.name}</TData>
      <TData>{data.passport}</TData>
      <TData>{data.country}</TData>
      <TData>{data.date}</TData>
      <TData month={month} style={month <= 3 ? { color: "red" } : null}>
        {data.expire}
      </TData>
      <TData
        className=""
        month={month}
        style={month <= 3 ? { color: "red" } : null}
      >
        {month} Mth
      </TData>
      <TData month={month}>
        {month > 3 ? "Safe" : month < 0 ? "Reject" : "Danger"}
      </TData>
      <TData>{data.lastupdate}</TData>
      <TData visible={visible}>
        <IconPanel visible={visible}>
          <Icon onClick={() => sentData("View")}>
            <HiOutlineArrowsPointingOut />
          </Icon>
          <Icon onClick={() => sentData("Edit")}>
            <TfiPencilAlt />
          </Icon>
          <Icon onClick={() => sentData("Delete")}>
            <MdDeleteOutline />
          </Icon>
          <Icon onClick={() => setVisible(0)}>
            <CgCloseR />
          </Icon>
        </IconPanel>
      </TData>
      <TData>
        <Icon onClick={() => setVisible(visible === 1 ? 0 : 1)}>
          <BsArrowDownLeftSquare />
        </Icon>
      </TData>
    </TRow>
  );
};

export default EmployeeTable2;

export const Table = styled.table({
  width: "100%",
  marginBottom: "10px",
});

export const THead = styled.thead({});

export const TBody = styled.tbody({});

export const TRow = styled.tr({
  padding: "0.3rem 0",
  background: "rgba(241, 242, 246,0.2)",
  border: "1px solid #ced4da",
  width: "100%",
  position: "relative",
});

export const TData = styled.td((props) => ({
  fontWeight: 400,
  fontSize: "1rem",
  padding: "0.5rem 1rem",
  whiteSpace: "wrap",
  position: "relative",
  ":nth-child(11)": {
    fontSize: "1rem",
    maxWidth: "50px !important",
    display: "none",
  },

  ":nth-child(6)": {
    color: "#333 !important",
    fontWeight: 400,
    "::before": {
      position: "absolute",
      content: "''",
      width: "2%",
      height: "30px",
      display: "block",
      top: "0",
      left: "0",
      bottom: "0",
      margin: "auto",
      color: "#333",
      background:
        props.month > 3
          ? "rgba(46, 213, 115, 1)"
          : props.month > 1
          ? "rgba(255, 165, 2,1)"
          : "rgba(234, 32, 39, 1)",
      borderRadius: "5px",
      "@media (min-width: 1200px)": {
        width: "2%",
      },
      "@media (max-width: 992px)": {
        width: "6%",
      },
    },
  },

  ":nth-child(10)": {},

  ":nth-child(1)": {
    width: "62px",
  },
  "@media (max-width: 1200px)": {
    fontSize: "0.9rem",
    padding: "0.5rem 0.3rem",
  },
  "@media (max-width: 992px)": {
    ":nth-child(9)": {
      display: "none",
    },
  },
  "@media (max-width: 890px)": {
    ":nth-child(4)": {
      display: "none",
    },
  },
  "@media (max-width: 768px)": {
    ":nth-child(8)": {
      display: "none",
    },
  },
  "@media (max-width: 687px)": {
    ":nth-child(5)": {
      display: "none",
    },
  },
  "@media (max-width: 551px)": {
    fontSize: "0.9rem",
    ":nth-child(1)": {
      width: "52px",
    },
    ":nth-child(3)": {
      display: "none",
    },
  },
  "@media (max-width: 450px)": {
    ":nth-child(7)": {
      display: "",
    },
    ":nth-child(11)": {
      display: "block",
      height: "100%",
      margin: "0.32rem 0.2rem 0.32rem 0.1rem",
      textAlign: "center",
      width: "30px",
    },
  },
}));

export const IconPanel = styled.div((props) => ({
  display: "grid",
  gridTemplateColumns: "auto auto auto",
  gap: "0.3rem",
  "@media (max-width: 450px)": {
    gridTemplateColumns: "auto auto auto auto",
    gap: "0.3rem 1rem",
    fontSize: "0.7rem",
    padding: "0.5rem 0.35rem",
    position: "absolute",
    content: "''",
    top: 0,
    right: 0,
    bottom: 0,
    background: "rgba(25, 42, 86, 1.0)",
    margin: "0.3rem",
    paddingLeft: "1rem",
    paddingRight: "1rem",
    borderRadius: "5px",
    color: "#fff",
    width: "200px",
    transition: "all ease-in 2s",
    display: props.visible === 0 ? "none" : "grid",
    zIndex: "1",
  },
}));

export const Image = styled.img({
  width: "30px",
  height: "auto",
  borderRadius: "5px",
  border: "2px solid rgba(255, 165, 2,1.0)",
});

export const Icon = styled.div({
  width: "25px",
  height: "25px",
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
  fontSize: "1.3rem",
  borderRadius: "2px",
  cursor: "pointer",
  ":nth-child(4)": {
    display: "none",

    "@media (max-width: 450px)": {
      display: "flex",
    },
  },
});

export const IconContainer = styled.div({
  position: "absolute",
  content: "''",
  width: "100%",
  height: "20px",
  background: "#705f93",
});

export const TData1 = styled(TData)({});
