import React, { useEffect, useState } from "react";
import styled from "styled-components";
import { TbSortAscending2, TbSortDescending2 } from "react-icons/tb";
import { EmpInformation } from "../data/EmpInfo";
import EmployeeTable2 from "../components/tables/EmployeeTable2";
import EditInfo from "../components/EditInformation/EditInfo";
import { useDispatch, useSelector } from "react-redux";
import secureLocalStorage from "react-secure-storage";
import { setLogin } from "../feature/UserSlice";
import { useNavigate } from "react-router-dom";
import LoadingPage from "../components/LoadingPage";

const EmplyeePage = () => {
  const [sortInfo, setSortInfo] = useState(1);
  const [searchValue, setSearchValue] = useState(null);
  const [rowSize, setRowSize] = useState(10);
  const [tablePage, setTablePage] = useState(1);
  const [totalPage, setTotalPage] = useState(0);
  const [disRow, setDisRow] = useState(0);
  const [pageIdx, setPageIdx] = useState(tablePage);
  const { data, myEvent, month, zone } = useSelector((state) => state.employee);
  const [Employees, setEmployees] = useState();
  const [evt, setEvt] = useState();
  const [loading, setLoading] = useState(true);
  const dispatch = useDispatch();
  const navigate = useNavigate();

  useEffect(() => {
    const data = secureLocalStorage.getItem("data");
    if (data !== null) {
      EmpInformation.map((dt) => {
        if (dt.username === data.username && dt.password === data.password) {
          dispatch(setLogin(data));
        }
        return null;
      });
    } else {
      navigate("/");
    }
    document.title = "Employee's Information";
    var rSize = localStorage.getItem("row");
    if (rSize === null) {
      if (window.innerWidth < 993) {
        setRowSize(10);
      }
      localStorage.setItem("row", rowSize);
    } else {
      setRowSize(rSize);
    }

    const flag = localStorage.getItem("sortby");

    if (flag === null) {
      localStorage.setItem("sortby", 1);
    } else {
      setSortInfo(flag);
    }

    const tp = sessionStorage.getItem("t-page");
    if (tp === null) {
      sessionStorage.setItem("t-page", 1);
    } else {
      setTablePage(tp);
    }
    const dr = sessionStorage.getItem("l-index");
    if (dr === null) {
      sessionStorage.setItem("l-index", 0);
    } else {
      setDisRow(dr);
    }
    setTotalPage(Math.ceil(EmpInformation.length / rowSize));
    setEmployees(EmpInformation);
    setEvt(myEvent);
    //setLoading(false);
  }, [searchValue, rowSize, myEvent, dispatch, navigate]);

  const handleData = (data) => {
    setLoading(true);
    setEmployees(data);
    setTimeout(() => {
      setLoading(false);
    }, 2000);
  };

  const rowChangeHendler = (e) => {
    setRowSize(e.target.value);
    localStorage.setItem("row", e.target.value);
  };

  const handleSort = () => {
    const flag = sortInfo === 1 ? 0 : 1;
    setSortInfo(flag);
    localStorage.setItem("sortby", flag);
  };

  const handlePrevious = () => {
    if (tablePage > 1) {
      handleCalculate(Number(tablePage) - 1);
    }
  };

  const handleNext = () => {
    if (totalPage > tablePage) {
      handleCalculate(Number(tablePage) + 1);
    }
  };

  const changeTablePage = (e) => {
    const val = e.target.value;
    console.log(val);
    handleCalculate(val);
  };

  const handleCalculate = (val) => {
    setPageIdx(val);
    if (totalPage >= val && val !== "") {
      setTablePage(val);
      sessionStorage.setItem("l-index", (Number(val) - 1) * rowSize);
      sessionStorage.setItem("t-page", Number(val));
      setDisRow((Number(val) - 1) * rowSize);
      setTablePage(Number(val));
    } else {
      //setTablePage(1);
    }
  };

  return (
    <React.Fragment>
      {loading ? (
        <LoadingPage />
      ) : (
        <>
          <Section>
            <Title>Employees Information</Title>
            <ActionContainer>
              <SortBy onClick={handleSort}>
                {sortInfo > 0 ? <TbSortAscending2 /> : <TbSortDescending2 />}
              </SortBy>
              <InputGroup>
                <Input
                  type="search"
                  placeholder="Search"
                  onChange={(e) => setSearchValue(e.target.value)}
                />
              </InputGroup>
            </ActionContainer>
            <TableContainer>
              <Table>
                <thead>
                  <TRow>
                    <THead>Img</THead>
                    <THead>Name</THead>
                    <THead>Passport</THead>
                    <THead>Country</THead>
                    <THead>Issue</THead>
                    <THead>Expiry</THead>
                    <THead>Month</THead>
                    <THead>Zone</THead>
                    <THead>Last Update</THead>
                    <THead>Action</THead>
                  </TRow>
                </thead>
                <tbody>
                  {Employees &&
                    Employees.sort((a, b) => {
                      var aexpiry = new Date(a.expire); // for ascending
                      var bexpiry = new Date(b.expire); // for ascending
                      if (sortInfo < 1) {
                        aexpiry = new Date(b.expire); // for descending
                        bexpiry = new Date(a.expire); // for descending
                      }
                      if (aexpiry < bexpiry) {
                        return -1;
                      } else {
                        return 1;
                      }
                    })
                      .filter((data) => {
                        var year = new Date(data.expire).getFullYear();
                        var mth = new Date(data.expire).getMonth();
                        const nyear = new Date();
                        var nyr = nyear.getFullYear();
                        var nmo = nyear.getMonth();

                        if (mth < nmo) {
                          year -= 1;
                          mth = mth + 12 - nmo;
                        } else {
                          mth = mth - nmo;
                        }
                        mth = (year - nyr) * 12 + mth;
                        if (
                          (searchValue === null ||
                            searchValue === "" ||
                            searchValue === undefined) &&
                          (data.status === "Active" ||
                            data.status === "Process" ||
                            data.status === "In-Active")
                        ) {
                          return data;
                        }
                        if (
                          Object.values(data)
                            .toLocaleString()
                            .toLocaleLowerCase()
                            .includes(searchValue.toLocaleLowerCase()) &&
                          data.status === "Active"
                        ) {
                          return data;
                        } else {
                          if (
                            "safe".includes(searchValue.toLocaleLowerCase()) &&
                            mth > 3
                          ) {
                            return data;
                          } else if (
                            "danger".includes(
                              searchValue.toLocaleLowerCase()
                            ) &&
                            mth < 4 &&
                            mth > 0
                          ) {
                            return data;
                          } else if (
                            "reject".includes(
                              searchValue.toLocaleLowerCase()
                            ) &&
                            mth < 1
                          ) {
                            return data;
                          } else if (
                            `${mth}`.includes(searchValue.toLocaleLowerCase())
                          ) {
                            return data;
                          } else if (
                            `${mth}mth`.startsWith(
                              searchValue.toLocaleLowerCase()
                            )
                          ) {
                            return data;
                          }
                          return null;
                        }
                      })
                      .slice(disRow, rowSize * tablePage)
                      .map((data, idx) => {
                        return <EmployeeTable2 key={data.id} data={data} />;
                      })}
                </tbody>
              </Table>
              <TableFooter>
                <Select name="row" value={rowSize} onChange={rowChangeHendler}>
                  <option value={5}>5</option>
                  <option value={10}>10</option>
                  <option value={20}>20</option>
                  <option value={50}>50</option>
                  <option value={100}>100</option>
                </Select>
                <PageNumber>Pages: 1 to {totalPage}</PageNumber>
                <ButtonContainer>
                  <Button disabled={tablePage === 1} onClick={handlePrevious}>
                    Previous
                  </Button>
                  <InputPageNumber
                    type="number"
                    onChange={changeTablePage}
                    value={pageIdx}
                  />
                  <Button
                    disabled={tablePage === totalPage}
                    onClick={handleNext}
                  >
                    Next
                  </Button>
                </ButtonContainer>
              </TableFooter>
            </TableContainer>
            {Object.values(data).length > 0 && (
              <EditInfo
                data={data}
                month={month}
                zone={zone}
                evt={evt}
                dataChange={handleData}
              />
            )}
          </Section>
        </>
      )}

      {/* {Object.values(data).length > 0 && myEvent !== "" ? (
        
      ) : null} */}
    </React.Fragment>
  );
};

export default EmplyeePage;

export const Section = styled.section({
  width: "100%",
  minHeight: "90vh",
  display: "flex",
  flexDirection: "column",
  padding: "0.5rem 1rem",
  position: "relative",
  paddingTop: "90px",
  "@media (max-width: 992px)": {
    paddingTop: "65px",
  },
});

export const TableContainer = styled.div({
  width: "100%",
  height: "auto",
  border: "1px solid rgba(3,3,3,0.2)",
  padding: "0.1rem 0.3rem",
});

export const ActionContainer = styled.div({
  width: "100%",
  display: "flex",
  alignItems: "center",
  border: "1px solid rgba(3,3,3,0.2)",
  padding: "0.3rem",
});

export const Title = styled.h1({
  fontSize: "1.3rem",
  fontWeight: 700,
  color: "rgba(3,3,3, 0.7)",
  marginBottom: "1rem",
});

export const SortBy = styled.div({
  width: "38px",
  height: "38px",
  border: "1px solid rgba(3,3,3, 0.5)",
  borderRadius: "3px",
  cursor: "pointer",
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
  fontSize: "1.5rem",
  marginRight: "0.5rem",
});

export const InputGroup = styled.div({
  position: "relative",
  display: "flex",
  flexWrap: "nowrap",
  alignItems: "stretch",
  marginLeft: "auto",
});

export const InputGroupText = styled.span({
  display: "flex",
  alignItems: "center",
  padding: "0.375rem 0.75rem",
  whiteSpace: "nowrap",
  fontSize: "1rem",
  lineHeight: 1.5,
  fontWeight: "400",
  textAlign: "center",
  color: "#fff",
  // background: "#e9ecef",
  background: "rgba(55, 66, 250,0.5)",
  border: "1px solid rgba(55, 66, 250,0.5)",
  // border: "1px solid #ced4da",
  // borderRadius: "0.25rem",
  borderTopRightRadius: 0,
  borderBottomRightRadius: 0,
});

export const Input = styled.input({
  padding: "0.5rem 1rem",
  border: "1px solid rgba(55, 66, 250,0.5)",
  // border: "1px solid #ced4da",
  width: "100%",
  fontSize: "1rem",
  // borderRadius: "0.25rem",
  borderTopLeftRadius: 0,
  borderBottomLeftRadius: 0,
  display: "block",
  ":focus": {
    outline: "none",
    //border: "1px dashed rgba(55, 66, 250,0.5)",
    // border: "1px dashed rgba(3,3,3,0.5)",
  },
});

export const Select = styled.select({
  padding: "0.5rem",
  maxWidth: "80px",
  border: "1px solid rgba(55, 66, 250,0.5)",
  // border: "1px solid #ced4da",
  width: "100%",
  fontSize: "1rem",
  // borderRadius: "0.25rem",
  borderTopLeftRadius: 0,
  borderBottomLeftRadius: 0,
  display: "block",
  margin: "0.3rem",
});

export const Table = styled.table({
  width: "100%",
  // tableLayout: "fixed",
});

export const THead = styled.th({
  fontWeight: 500,
  fontSize: "1rem",
  padding: "0.5rem 1rem",
  whiteSpace: "wrap",
  textAlign: "left",
  ":nth-child(1)": {
    width: "62px",
  },
  "@media (max-width: 1200px)": {
    fontSize: "0.9rem",
    padding: "0.5rem 0.3rem",
  },
  "@media (max-width: 992px)": {
    ":nth-child(9)": {
      display: "none",
    },
  },
  "@media (max-width: 890px)": {
    ":nth-child(4)": {
      display: "none",
    },
  },
  "@media (max-width: 768px)": {
    ":nth-child(8)": {
      display: "none",
    },
  },
  "@media (max-width: 687px)": {
    ":nth-child(5)": {
      display: "none",
    },
  },
  "@media (max-width: 551px)": {
    fontSize: "0.9rem",
    ":nth-child(1)": {
      width: "52px",
    },
    ":nth-child(3)": {
      display: "none",
    },
  },
  "@media (max-width: 450px)": {
    fontSize: "0.7rem",
    padding: "0.5rem 0.35rem",
    ":nth-child(10)": {
      display: "none",
    },
    ":nth-child(7)": { display: "block" },
  },
});

export const TRow = styled.tr({
  padding: "0.3rem 0",
  background: "rgba(241, 242, 246,0.2)",
  border: "1px solid #ced4da",
  width: "100%",
});

export const TData = styled.td((props) => ({
  fontWeight: 400,
  fontSize: "1rem",
  padding: "0.5rem 1rem",
  whiteSpace: "wrap",
  position: "relative",

  ":nth-child(6)": {
    color: "#333 !important",
    fontWeight: 400,
    "::before": {
      position: "absolute",
      content: "''",
      width: "2%",
      height: "30px",
      display: "block",
      top: "0",
      left: "0",
      bottom: "0",
      margin: "auto",
      color: "#333",
      background:
        props.month > 3
          ? "rgba(46, 213, 115, 1)"
          : props.month > 1
          ? "rgba(255, 165, 2,1)"
          : "rgba(234, 32, 39, 1)",
      borderRadius: "5px",
      "@media (min-width: 1200px)": {
        width: "2%",
      },
      "@media (max-width: 992px)": {
        width: "6%",
      },
    },
  },

  ":nth-child(1)": {
    width: "62px",
  },
  "@media (max-width: 768px)": {
    ":nth-child(7)": {
      display: "none",
    },
  },
  "@media (max-width: 687px)": {
    ":nth-child(4)": {
      display: "none",
    },
  },
  "@media (max-width: 550px)": {
    fontSize: "0.9rem",
    ":nth-child(1)": {
      width: "62px",
    },
  },
  "@media (max-width: 450px)": {
    fontSize: "0.7rem",
    padding: "0.5rem 0.35rem",
  },
}));

export const Image = styled.img({
  width: "30px",
  height: "auto",
  borderRadius: "5px",
  border: "2px solid rgba(255, 165, 2,1.0)",
});

export const TableFooter = styled.div({
  display: "flex",
  alignItems: "center",
  justifyContent: "flex-end",
  width: "100%",
});

export const ButtonContainer = styled.div({
  display: "flex",
  flexDirection: "row",
  alignItems: "center",
  margin: "0 0.3rem",
});

export const Button = styled.button({
  padding: "0.5rem 1rem",
  border: "none",
  outline: "none",
  fontWeight: 600,
  borderRadius: "3px",
  color: "#fff",
  "&:nth-child(1)": {
    background: "#8a5082",
  },
  "&:nth-child(3)": {
    background: "rgba(255, 71, 87,1.0)",
  },
  "@media (max-width: 450px)": {
    padding: "0.5rem 0.7rem",
    fontSize: "0.8rem",
  },
});

const InputPageNumber = styled(Input)({
  margin: "0 0.3rem",
  width: "50px",
  padding: "0.5rem 0.3rem",
  textAlign: "center",
  fontWeight: 600,
});

export const PageNumber = styled.p({
  padding: "0 0.5rem",
  fontWeight: 500,
  opacity: 0.7,
  "@media (max-width: 450px)": {
    fontSize: "0.8rem",
  },
});
