import React, { useEffect, useState } from "react";
import styled from "styled-components";
import { useDispatch } from "react-redux";
import { reset } from "../../feature/EmploySlice";
import { IoCloseOutline } from "react-icons/io5";
import { EmpInformation } from "../../data/EmpInfo";
import WarningIcon from "../Image/warning-1.png";
import TickIcon from "../Image/check.png";

const EditInfo = ({ data, month, zone, evt, dataChange }) => {
  const [info, setInfo] = useState({
    id: data.id,
    name: data.name,
    passport: data.passport,
    country: data.country,
    date: data.date,
    expire: data.expire,
    lastupdate: new Date().toISOString().substring(0, 10),
    image: data.image,
    status: data.status,
  });
  const dispatch = useDispatch();
  const [event, setEvent] = useState(evt);
  const [confirmDelete, setConfirmDelete] = useState(false);
  const [isUpdate, setIsUpdate] = useState(false);
  const [image, setImage] = useState(data.image);

  useEffect(() => {
    setEvent(evt);
  }, [evt]);

  const handleSubmit = (e) => {
    e.preventDefault();
    const Idx = EmpInformation.findIndex((dt) => dt.id === data.id);
    EmpInformation[Idx] = info;
    setIsUpdate(true);
  };

  const handleChange = (e) => {
    setInfo({ ...info, [e.target.name]: e.target.value });
    if (e.target.name === "image") {
      const reader = new FileReader();
      reader.readAsDataURL(e.target.files[0]);

      reader.onload = () => {
        if (reader.readyState === 2) {
          const img = reader.result;
          //writeFile(".././../../public/image", img, "base64");
          setImage(img);
        }
      };
    }
  };

  const handleDelete = () => {
    const Idx = EmpInformation.findIndex((dt) => dt.id === data.id);
    EmpInformation.splice(Idx, 1);
    setConfirmDelete(true);
  };

  return (
    <React.Fragment>
      {event === "View" ? (
        <Container>
          <InnerContainer>
            <Icon onClick={() => dispatch(reset())}>
              <IoCloseOutline />
            </Icon>
            <Title>Employee Information</Title>
            <SubContainer>
              <Image src={data.image} alt={data.name} />
              <PersonalInfoContainer>
                <Text1>{data.name}</Text1>
                <H2>Passport: {data.passport}</H2>
                <P>Country: {data.country}</P>
                <Status>Status: {data.status}</Status>
              </PersonalInfoContainer>
            </SubContainer>
            <SubContainer2>
              <DataContainer>
                <Label>Issue Date:</Label>
                <Text>{data.date}</Text>
                <Label>Expiry Date:</Label>
                <Text>{data.expire}</Text>
              </DataContainer>
              <DataContainer>
                <Label>Last Update:</Label>
                <Text>{data.lastupdate}</Text>
                <Label>Available Month:</Label>
                <Text>{month < -1 ? "NaN" : `${month} Months`}</Text>
              </DataContainer>
              <DataContainer>
                <Label>Zone:</Label>
                <Text>{zone}</Text>
              </DataContainer>
            </SubContainer2>
            <ActionContainer>
              <Button onClick={() => dispatch(reset())}>Cancel</Button>
              <Button onClick={() => setEvent("Edit")}>Edit</Button>
              <Button onClick={() => setEvent("Delete")}>Delete</Button>
            </ActionContainer>
          </InnerContainer>
        </Container>
      ) : null}
      {event === "Edit" ? (
        <React.Fragment>
          {isUpdate !== true ? (
            <Container1>
              <InnerContainer>
                <Icon onClick={() => dispatch(reset())}>
                  <IoCloseOutline />
                </Icon>
                <EmpForm onSubmit={handleSubmit}>
                  <Title>Update Information</Title>
                  <InputGroup>
                    <DataContainer>
                      <Label1>Name</Label1>
                      <Input
                        type="name"
                        name="name"
                        value={info.name}
                        onChange={handleChange}
                      />
                      <Label1>Passport No</Label1>
                      <Input
                        type="name"
                        name="passport"
                        value={info.passport}
                        onChange={handleChange}
                      />
                      <Label1>Country</Label1>
                      <Input
                        type="name"
                        name="country"
                        value={info.country}
                        onChange={handleChange}
                      />
                      <Label1>Status</Label1>
                      <Select
                        name="status"
                        value={info.status}
                        onChange={handleChange}
                      >
                        <option value="Active">Active</option>
                        <option value="Rejected">Rejected</option>
                        <option value="Process">Process</option>s
                      </Select>
                      <Label1>Visa Issue Date</Label1>
                      <Input
                        type="date"
                        name="date"
                        value={info.date}
                        onChange={handleChange}
                      />
                      <Label1>Visa Expiry Date</Label1>
                      <Input
                        type="date"
                        name="expire"
                        value={info.expire}
                        onChange={handleChange}
                      />
                    </DataContainer>
                    <DataContainer className="image-cont">
                      <h5 className="mb-3">Update Image</h5>
                      <ImageContainer>
                        <Image
                          src={image}
                          alt={data.name}
                          onChange={handleChange}
                        />
                      </ImageContainer>
                      <Input
                        style={{ display: "none" }}
                        type="file"
                        id="img"
                        name="image"
                        accept="image/*"
                        onChange={handleChange}
                      />
                      <LabelButton htmlFor="img">Upload</LabelButton>
                    </DataContainer>
                  </InputGroup>
                  <ActionContainer>
                    <Button
                      type="submit"
                      style={{
                        background: "rgb(108, 45, 199)",
                        width: "150px",
                      }}
                    >
                      Update
                    </Button>
                    <Button
                      onClick={() => dispatch(reset())}
                      style={{ background: "rgb(250, 42, 85)", width: "150px" }}
                    >
                      Cancel
                    </Button>
                  </ActionContainer>
                </EmpForm>
              </InnerContainer>
            </Container1>
          ) : (
            <Container>
              <DeleteContainer>
                <HeaderTitle>Update User Successfully</HeaderTitle>
                <Icon1
                  onClick={() => {
                    dataChange(EmpInformation);
                    dispatch(reset());
                  }}
                >
                  <IoCloseOutline />
                </Icon1>
                <DeleteSubContainer>
                  <ImageIcon src={TickIcon} alt="ok" />
                  <DeleteInfo>Update Successfully.</DeleteInfo>
                </DeleteSubContainer>
                <ButtonContainer>
                  <Button1
                    style={{
                      padding: "0.5rem 2rem",
                      background: "rgba(72, 52, 212,1.0)",
                    }}
                    onClick={() => {
                      dataChange(EmpInformation);
                      dispatch(reset());
                    }}
                  >
                    Ok
                  </Button1>
                </ButtonContainer>
              </DeleteContainer>
            </Container>
          )}
        </React.Fragment>
      ) : null}

      {event === "Delete" ? (
        <Container>
          {confirmDelete !== true ? (
            <DeleteContainer>
              <HeaderTitle>Delete User</HeaderTitle>
              <Icon1 onClick={() => dispatch(reset())}>
                <IoCloseOutline />
              </Icon1>
              <DeleteSubContainer>
                <ImageIcon src={WarningIcon} alt="Delete" />
                <DeleteInfo>Do you want to delete this?</DeleteInfo>
              </DeleteSubContainer>
              <ButtonContainer>
                <Button1
                  style={{ background: "rgb(250, 42, 85)" }}
                  onClick={() => dispatch(reset())}
                >
                  Cancel
                </Button1>
                <Button1
                  style={{
                    padding: "0.5rem 2rem",
                    background: "rgba(72, 52, 212,1.0)",
                  }}
                  onClick={handleDelete}
                >
                  Ok
                </Button1>
              </ButtonContainer>
            </DeleteContainer>
          ) : (
            <DeleteContainer>
              <HeaderTitle>Delete User Successfully</HeaderTitle>
              <Icon1
                onClick={() => {
                  dataChange(EmpInformation);
                  dispatch(reset());
                }}
              >
                <IoCloseOutline />
              </Icon1>
              <DeleteSubContainer>
                <ImageIcon src={TickIcon} alt="ok" />
                <DeleteInfo>Delete Successfully.</DeleteInfo>
              </DeleteSubContainer>
              <ButtonContainer>
                <Button1
                  style={{
                    padding: "0.5rem 2rem",
                    background: "rgba(72, 52, 212,1.0)",
                  }}
                  onClick={() => {
                    dataChange(EmpInformation);
                    dispatch(reset());
                  }}
                >
                  Ok
                </Button1>
              </ButtonContainer>
            </DeleteContainer>
          )}
        </Container>
      ) : null}
    </React.Fragment>
  );
};

export default EditInfo;

export const Container1 = styled.div({
  position: "absolute",
  top: "80px",
  left: 0,
  right: 0,
  bottom: 0,
  margin: "auto",
  width: "100%",
  minHeight: "100vh",
  display: "flex",
  alignItems: "baseline",
  justifyContent: "center",
  background: "rgba(3,3,3, 0.7)",
  transition: "all ease-out 1s",
  overflow: "scroll",
  paddingTop: "45px",
  "@media (max-width: 992px)": {
    paddingTop: "80px",
  },
  "@media (max-width: 540px)": {
    top: "55px",
    paddingTop: "5px",
  },
});

export const Container = styled(Container1)({
  alignItems: "baseline",
  "@media (max-width: 540px)": {
    alignItems: "baseline",
  },
});

export const InnerContainer = styled.div({
  width: "40%",
  height: "auto",
  padding: "1rem 2rem",
  display: "flex",
  flexDirection: "column",
  background: "#fff",
  minHeight: "500px",
  borderRadius: "5px",
  transition: "all ease 1s",
  position: "relative",
  border: "1px solid rgba(27, 20, 100,1.0)",
  overflow: "hidden",
  ":nth-child(2)": {
    "@media (max-width: 1100px)": {
      width: "60%",
    },
    "@media (max-width: 850px)": {
      width: "70%",
    },
    "@media (max-width: 540px)": {
      width: "95%",
      padding: "0.5rem",
    },
  },
  "@media (max-width: 992px)": {
    width: "50%",
  },
  "@media (max-width: 850px)": {
    width: "70%",
  },
  "@media (max-width: 540px)": {
    width: "95%",
    padding: "0.5rem",
  },
});

export const Title = styled.h1({
  fontSize: "1.3rem",
});

export const SubContainer = styled.div({
  display: "grid",
  gridTemplateColumns: "160px auto",
  gap: "0.3rem 0.5rem",
  marginTop: "0.5rem",
  "@media (max-width: 540px)": {
    width: "95%",
    padding: "0.5rem",
    gridTemplateColumns: "120px auto",
  },
});

export const Image = styled.img({
  width: "98%",
  height: "auto",
});
export const ImageContainer = styled.div({
  width: "120px",
  height: "130px",
  borderRadius: "5px",
  border: "1px dashed rgba(255, 165, 2,1.0)",
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
});

export const PersonalInfoContainer = styled.div({
  display: "flex",
  flexDirection: "column",
  gap: "0.3rem",
  overflow: "hidden",
});

export const Text1 = styled.h1({
  fontSize: "1.25rem",
  fontWeight: "700",
});

export const H2 = styled.h2({
  fontSize: ".97rem",
  fontWeight: "600",
});

export const P = styled.p({
  fontSize: ".9rem",
  fontWeight: "500",
});

export const Status = styled(P)({
  position: "relative",
});

export const SubContainer2 = styled.div({
  display: "grid",
  gridTemplateColumns: "auto auto auto",
  "@media (max-width: 540px)": {
    gridTemplateColumns: "auto auto",
  },
});

export const DataContainer = styled.div({
  display: "flex",
  flexDirection: "column",
  msFlexDirection: "column",
  WebkitFlexDirection: "column",
  padding: "0.7rem 0.3rem",
  overflow: "hidden",
});

export const Label = styled.label({
  fontSize: "0.8rem",
  fontWeight: 500,
  "@media (max-width: 992px)": {
    fontSize: "0.7rem",
  },
});
export const Label1 = styled.label({
  fontSize: "0.8rem",
  fontWeight: 500,
  marginTop: "0.7rem",
  ":nth-child(1)": {
    marginTop: 0,
  },
  "@media (max-width: 992px)": {
    fontSize: "0.7rem",
  },
});

export const Text = styled.h4({
  fontWeight: "500",
  fontSize: "1.1rem",
  marginBottom: "1rem",
  "@media (max-width: 992px)": {
    fontSize: "1rem",
  },
  "@media (max-width: 600px)": {
    fontSize: "1rem",
  },
});

export const Icon = styled.div({
  width: "45px",
  height: "35px",
  position: "absolute",
  top: "5px",
  right: "5px",
  border: "1px solid red",
  fontSize: "1.6rem",
  display: "flex",
  textAlign: "center",
  alignItems: "center",
  justifyContent: "center",
  margin: "auto",
  borderRadius: "5px",
  color: "red",
  cursor: "pointer",
  ":hover": {
    background: "red",
    color: "#fff",
  },
});

export const Icon1 = styled(Icon)({
  border: "1px solid #fff",
  color: "#fff",
});

export const ActionContainer = styled.div({
  display: "flex",
  width: "100%",
  flexDirection: "row",
  msFlexDirection: "row",
  WebkitFlexDirection: "row",
  alignItems: "center",
  WebkitAlignItems: "center",
  justifyContent: "center",
  WebkitJustifyContent: "center",
  padding: "1rem 0.3rem",
  gap: "0.5rem",
});

export const Button = styled.button({
  padding: "0.5rem 1rem",
  border: "none",
  outline: "none",
  fontSize: ".9rem",
  fontWeight: "500",
  color: "#fff",

  ":nth-child(1)": { background: "rgb(128, 0, 128)" },
  ":nth-child(2)": { background: "rgb(108, 45, 199)" },
  ":nth-child(3)": { background: "rgb(250, 42, 85)" },
});

export const Input = styled.input({
  padding: "0.5rem 1rem",
  border: "1px solid rgba(55, 66, 250,0.5)",
  // border: "1px solid #ced4da",
  width: "100%",
  fontSize: "1rem",
  // borderRadius: "0.25rem",
  borderTopLeftRadius: 0,
  borderBottomLeftRadius: 0,
  display: "block",
  ":focus": {
    outline: "none",
    border: "1px dashed rgba(55, 66, 250,0.5)",
    // border: "1px dashed rgba(3,3,3,0.5)",
  },
});

export const Select = styled.select({
  padding: "0.5rem 1rem",
  border: "1px solid rgba(55, 66, 250,0.5)",
  // border: "1px solid #ced4da",
  width: "100%",
  fontSize: "1rem",
  // borderRadius: "0.25rem",
  borderTopLeftRadius: 0,
  borderBottomLeftRadius: 0,
  display: "block",
  ":focus": {
    outline: "none",
    border: "1px dashed rgba(55, 66, 250,0.5)",
    // border: "1px dashed rgba(3,3,3,0.5)",
  },
});

export const EmpForm = styled.form({
  display: "grid",
  gridTemplateColumns: "auto",
});
export const InputGroup = styled.div({
  display: "grid",
  gridTemplateColumns: "auto auto",
  gap: "0.5rem",
  "@media (max-width: 992px)": {
    gridTemplateColumns: "auto",
  },
});

export const LabelButton = styled.label({
  padding: "0.5rem 1rem",
  border: "none",
  outline: "none",
  fontSize: ".9rem",
  fontWeight: "500",
  color: "#fff",
  marginTop: "0.5rem",
  background: "rgb(128, 0, 128)",
});

export const FlexRow = styled.div((props) => ({
  display: "flex",
  flexDirection: "row",
  msFlexDirection: "row",
  WebkitFlexDirection: "row",
  alignItems: props.alignItemsCenter ? "center" : "baseline",
  WebkitAlignItems: props.alignItemsCenter ? "center" : "baseline",
  justifyContent: props.justifyContentCenter ? "center" : "",
  WebkitJustifyContent: props.justifyContentCenter ? "center" : "",
  flexWrap: props.nowrap ? "nowrap" : "wrap",
  WebkitFlexWrap: props.nowrap ? "nowrap" : "wrap",
}));
export const FlexColumn = styled.div((props) => ({
  display: "flex",
  flexDirection: "column",
  msFlexDirection: "column",
  WebkitFlexDirection: "column",
  alignItems: props.alignItemsCenter ? "center" : "baseline",
  WebkitAlignItems: props.alignItemsCenter ? "center" : "baseline",
  justifyContent: props.justifyContentCenter ? "center" : "",
  WebkitJustifyContent: props.justifyContentCenter ? "center" : "",
}));

export const DeleteContainer = styled.div({
  width: "540px",
  height: "auto",
  background: "#fff",
  borderRadius: "5px",
  padding: "0.5rem",
  paddingTop: "3.5rem",
  position: "relative",
  minHeight: "50px",
  display: "flex",
  flexDirection: "column",
  msFlexDirection: "column",
  WebkitFlexDirection: "column",
  marginTop: "150px",
  // alignItems: props.alignItemsCenter ? "center" : "baseline",
  // WebkitAlignItems: props.alignItemsCenter ? "center" : "baseline",
  // justifyContent: props.justifyContentCenter ? "center" : "",
  // WebkitJustifyContent: props.justifyContentCenter ? "center" : "",
  "@media (max-width: 540px)": {
    width: "90%",
    marginTop: "150px",
  },
});

export const HeaderTitle = styled.p({
  position: "absolute",
  left: 0,
  top: 0,
  margin: "auto 0",
  fontSize: "0.9rem",
  fontWeight: "400",
  color: "#fff",
  background: "#576574",
  width: "100%",
  lineHeight: "3.1",
  borderTopLeftRadius: "5px",
  borderTopRightRadius: "5px",
  paddingLeft: "10px",
});

export const DeleteSubContainer = styled.div({
  display: "grid",
  padding: "1rem",
  gridTemplateColumns: "50px auto",
  gap: "0.3rem",
  alignItems: "center",
});

export const ImageIcon = styled.img({
  width: "95%",
  height: "auto",
});

export const DeleteInfo = styled.p({
  fontSize: "1.1rem",
});

export const ButtonContainer = styled.div({
  padding: "1rem 0.3rem",
  display: "flex",
  flexDirection: "row",
  msFlexDirection: "row",
  WebkitFlexDirection: "row",
  alignItems: "center",
  WebkitAlignItems: "center",
  justifyContent: "center",
  WebkitJustifyContent: "center",
  gap: "0.5rem 1rem",
});

export const Button1 = styled.button({
  padding: "0.5rem 1rem",
  border: "none",
  outline: "none",
  fontSize: ".9rem",
  fontWeight: "500",
  color: "#fff",
});
